<template>
  <div id="app">

      <!-- Screenshot Modal -->
      <modal id="screenModal" v-cloak>
          <template slot="title">
              Screenshot herunterladen
          </template>

          <div slot="body">
              <p>
                  Rechtsklicke auf das Bild -> Speichern unter. Das Bild ist optimiert um auf Pr0 hochgeladen zu werden.
              </p>

              <img :src="screenShotToDownload" alt="Canvas">
          </div>
      </modal>

      <div class="text-white bg-dark p-2">
          <div  class="text-center">
              <span>Fragen? Anregungen? Schreib mir: <a href="https://pr0gramm.com/user/PoTTii" target="_blank">@PoTTii</a></span>
          </div>
          <div v-if="pollDataLoaded">
              <div class="container">
                  <div class="row mt-4">
                      <div class="col-md-4">
                          <button class="btn btn-secondary" @click="reset">Andere Umfrage auswerten</button><br>
                          <button class="btn btn-secondary mt-2" @click="downloadScreenshot">Screenshot herunterladen</button>
                      </div>
                      <div class="col-md-8 text-right">
                          <strong>Einstellungen:</strong><br>

                          <div class="form-check">
                              <input class="form-check-input" type="checkbox" id="options-detail" v-model="options.details">
                              <label class="form-check-label" for="options-detail">
                                  Antwort-Details anzeigen
                              </label>
                          </div>

                          <div class="row mt-3">
                              <div class="col-md-6 offset-md-6 float-right">
                                  Farbe für Label: &nbsp;
                                  <ColorPicker :color="options.labelFontColor" v-model="options.labelFontColor" />
                                  &nbsp; <span class="badge badge-light mouseHover" @click="options.labelFontColor = '#fff'">Auf Standard setzen</span>
                              </div>
                          </div>

                          <div class="row">
                              <div class="col-md-6 offset-md-6 float-right">
                                  Hintergrundfarbe: &nbsp;
                                  <ColorPicker :color="options.bgColor" v-model="options.bgColor" />
                                  &nbsp; <span class="badge badge-light mouseHover" @click="options.bgColor = '#161618'">Auf Standard setzen</span>
                              </div>
                          </div>
                          <div class="row">
                              <div class="col-md-6 offset-md-6 float-right">
                                  Titelfarbe: &nbsp;
                                  <ColorPicker :color="options.titleColor" v-model="options.titleColor" />
                                  &nbsp; <span class="badge badge-light mouseHover" @click="options.titleColor = '#ee4d2e'">Auf Standard setzen</span>
                              </div>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
      </div>

      <div class="container">
          <div class="row mt-4" v-if="!pollDataLoaded">
              <div class="col-md-12">
                  <h1 class="text-center">Pr0p0ll Viewer</h1>
                  <h3>Howto: JSON-Code von pr0p0ll kopieren</h3>
                  <p>Um dieses Tool nutzen zu können musst du nachdem deine Umfrage beendet ist den JSON-Code kopieren.</p>
                  <p>Du findest diesen indem du bei pr0p0ll auf "Eigene" gehst und anschliessend bei der gewünschten Umfrage auf "Ergebnisse ansehen".</p>
                  <p>Klicke danach auf "Ergebnisse ansehen" und kopiere den ganzen Code den du siehst mit STRG + A und STRG + C.</p>
                  <p>Füge den Code im unteren Feld ein um deine Ergebnisse in Charts zu sehen.</p>
                  <hr>
                  <label for="jsonInput">Füge hier deinen JSON-Code von pr0p0ll ein:</label>
                  <textarea  id="jsonInput" cols="30" rows="10" class="form-control" v-model="pollDataString"></textarea>
                  <button class="btn btn-primary mt-4" @click="loadPollData">Laden</button>
              </div>
          </div>

          <div v-else>
              <div class="row mt-4">
                  <div class="col-md-12" id="screenshotContainer" style="width: 1052px; margin-left: auto; margin-right: auto;" :style="{ backgroundColor: options.bgColor}" v-if="rendered">
                      <poll-info :data="pollData.info" v-if="pollData.info"></poll-info>
                      <hr>
                      <question :data="question" v-for="question in questions" :key="question.id"></question>
                  </div>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
    import pollInfo from './components/pollinfo';
    import question from './components/question';
    import html2canvas from 'html2canvas';
    import ColorPicker from './components/external/ColorPicker';

    export default {
        name: 'app',
        components: {
            pollInfo, question, ColorPicker
        },
        data() {
            return {
                pollData: {},
                pollDataString: "",
                pollDataLoaded: false,
                options: {
                    details: false,
                    labelFontColor: '#fff',
                    bgColor: '#161618',
                    titleColor: '#ee4d2e'
                },
                screenShotToDownload: '',
                rendered: false
            }
        },
        computed: {
            questions() {
                let obj = JSON.parse(JSON.stringify(this.pollData));
                delete obj.info;
                return obj;
            },
            labelColorComputed() {
                return this.options.labelFontColor;
            },
            setTitleColor() {
                return 'color: ' + this.options.titleColor + ';';
            }
        },
        watch: {
            labelColorComputed() {
                this.rerender();
            }
        },
        methods: {
            rerender() {
                this.rendered = false;
                this.$nextTick(() => {
                    this.rendered = true;
                });
            },
            reset() {
                this.pollData = {};
                this.pollDataString = "";
                this.pollDataLoaded = false;
                this.rendered = false;

                this.screenShotToDownload = '';
            },
            downloadScreenshot() {
                html2canvas(document.querySelector("#screenshotContainer"), {backgroundColor: this.options.bgColor}).then(canvas => {
                    this.screenShotToDownload = canvas.toDataURL('image/png');
                    // eslint-disable-next-line
                    VoerroModal.show('screenModal');
                });
            },
            async checkPollData() {
                if(this.pollDataString.length < 1 || !this.isValidJson(this.pollDataString))
                    return false;

                this.pollData = JSON.parse(this.pollDataString);
                return true;
            },
            loadPollData() {
                const vm = this;
                return this.checkPollData().then(() => {
                    let obj = this.pollData;
                    if(typeof obj === 'object'){
                        this.pollDataLoaded = true;
                        this.rendered = true;
                        return true;
                    }
                    throw 'error';
                }).catch(() => {
                    return vm.reportInvalidJson();
                });
            },
            reportInvalidJson() {
                this.pollDataString = "";
                this.pollDataLoaded = false;
                alert("Dein eingefügte JSON Code ist ungültig. Vergewissere dich dass du ALLES kopiert hast.");
                throw "error";
            },
            isValidJson(str) {
                try {
                    JSON.parse(str);
                } catch (e) {
                    this.reportInvalidJson();
                    return false;
                }
                return true;
            }
        }
    }
</script>
